@mixin default_font-family {
	font-family: "Zen Kaku Gothic Antique", sans-serif;
	font-weight: 500;
}

@mixin zenkaku_r {
	font-family: "Zen Kaku Gothic Antique", sans-serif;
	font-weight: 400;
}

@mixin zenkaku_m {
	font-family: "Zen Kaku Gothic Antique", sans-serif;
	font-weight: 500;
}

@mixin zenkaku_b {
	font-family: "Zen Kaku Gothic Antique", sans-serif;
	font-weight: 700;
}

@mixin jost_m {
	font-family: Jost, sans-serif;
	font-weight: 500;
	font-optical-sizing: auto;
	font-style: normal;
}

@mixin jost_sb {
	font-family: Jost, sans-serif;
	font-weight: 600;
	font-optical-sizing: auto;
	font-style: normal;
}

@mixin jost_b {
	font-family: Jost, sans-serif;
	font-weight: 700;
	font-optical-sizing: auto;
	font-style: normal;
}

